.advantages {
    min-height: 590px;
    padding: 57px 0px 51px 0;
    background-color: $background_color;
    &__wrapper {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-top: 32px;
    }
    &__item {
        display: flex;
        justify-content: start;
        align-items: start;
        width: 520px;
        margin-bottom: 35px;
    }
    &__img {
        margin-right: 25px;
    }
    &__text {
        font-weight: 300;
        font-size: 14px;
        margin-top: 7px;
        color: #3a3a3a;
    }
}
@media screen and (max-width: 1200px) {
    .advantages {
        &__item {
            flex-direction: column;
            width: 400px;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin: 10px;
        }
        &__text {
            font-size: 13px;
        }
    }
}