.works {
    padding: 51px 0 58px 0;
    min-height: 620px;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: start;
        margin-top: 37px;
    }
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        width: 355px;
        overflow-wrap: break-word;
        img {
            width: 100%;
        }
    }
    &__img {
        margin-bottom: 25px;
    }
    &__text {
        font-size: 14px;
        font-weight: 300;
        margin-top: 12px;
    }
}
@media screen and(max-width: 1200px) {
    .works {
        &__item {
            width: 280px;   
        }
    }
}
@media screen and(max-width: 992px) {
    .works {
        &__item {
            width: 320px;
            margin: 15px 0; 
        }
        &__wrapper {
            flex-direction: column;
            align-items: center;
        }
    }
}