.question {
    min-height: 440px;
    padding: 64px 0 80px 0;
    margin: 0 auto;
    background: url(../img/backgrounds/form-bg.webp) center center/cover no-repeat;
    &__form {
        width: 100%;
        height: 300px;
        background-color: rgba(193, 194, 195, .85);
    }
    &__descr {
        height: 70px;
        background-color: $background_dark_color;
        text-align: center;
        color: #fff;
        text-transform: uppercase;
    }
    &__title {
        font-size: 19px;
        line-height: 19px;
        font-weight: 700;
        padding-top: 20px;
    }
    &__subtitle {
        font-size: 14px;
        line-height: 14px;
    }
    form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        padding: 0px 39px;
        textarea {
            width: 100%;
            height: 70px;
            margin-top: 5px;
            border-radius: 5px;
            border: 1px solid #c9c9c9;
            resize: none;
        }
        button {
            margin-top: 16px;
        }
    }
}
@media screen and(max-width: 992px) {
    .question {
        &__form {
            height: 420px;
        }
        form {
            flex-direction: column;
            input {
                width: 100%;
            }
            textarea {
                margin-top: 16px;
            }
        }
    }
}