.overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .7);
    display: none;
}
.modal {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 400px;
    width: 350px;
    background-color: $background_color;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 15px;
    &__close {
        font-size: 28px;
        font-weight: 400;  
        color: $main_color;
        position: absolute;
        right: 20px;
        top: 10px;
        cursor: pointer;
        transition: .3s all;
        &:hover {
            color: rgba(236, 100, 75, .5);
        }
    }
    &__subtitle {
        font-size: 20px;
        color: $main_color;
    }
    &__descr {
        font-weight: 300;
        color: $main_color;
    }
    form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        button {
            margin-top: 16px;
        }
    }
}
#thanks {
    display: none;
}