.container {
    max-width: 1140px;
    margin: 0 auto;
}
#header {
    background: url(../img/backgrounds/main-bg.webp) center center/cover no-repeat;
    min-height: 680px;
    padding: 0 0 82px 0;
}
.header {
    padding-top: 20px;
    &__wrapper {
        max-width: 1140px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__navigation {
        height: 100px;
        display: flex;
    }
    .hamburger {
        display: none;
    }
    &__menu {
        display: flex;
        align-items: center;
        font-size: 15px;
        padding-left: 0;
        margin-left: 0;
    }
    &__logo {
        margin-right: 10px;
    }
        a {
            color: #fff;
            transition: .3s all;
            margin: 0 5px;
            border-right: 2px dotted #858891;
            padding-right: 7px;
            &:last-of-type {
                border: none;
            }
            &:hover {
                text-decoration: none;
                color: gray;
            }
    }
        
}
.main {
    color: #fff;
    &__descr {
        text-align: center;
    }
    &__title {
        font-size: 24px;
        font-weight: 300;
    }
    &__header {
        font-size: 72px;
        color: $main_color;
        margin-top: 20px;
        line-height: 72px;
    }
    &__subheader {
        font-size: 42px;
        font-weight: 400;
        margin-top: 10px;
        line-height: 32px;
    }
    &__divider {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 38px;
        hr {
            width: 388px;
            color: #fff;
            margin: 0;
        }
        span {
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 4px;
            background-color: #fff;
            margin: 0 11px;
        }
    }
    &__icons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 30px;
    }
    &__item {
        text-align: center;
        width: 250px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        font-weight: 300;
        img {
            margin-bottom: 20px;
        }
    }
    &__button {
        display: block;
        margin: 0 auto;
        margin-top: 42px;
    }
}
@media screen and (max-width: 1200px) {
    .header {
        &__menu {
            font-size: 12px;
        }
        &__link {
            margin: 0;
            padding: 0 5px;
        }
    }
    .main {
        &__title {
            font-size: 20px;
        }
        &__header {
            font-size: 60px;
        }
        &__subheader {
            font-size: 36px;
        }
        &__icons {
            margin-top: 20px;
        }
        &__item {
            font-size: 13px;
            margin: 0 5px;
            img {
                margin-bottom: 15px;
            }
        }
    }
}
@media screen and (max-width: 992px) {  
    .header {
        &__wrapper {
            max-width: 750px;
        }
        .contacts {
            display: none;
        }
    }
    .main {
        &__header {
            font-size: 48px;
            line-height: 48px;
        }
        &__subheader {
            font-size: 30px;
            line-height: 30px;
        }
        &__item {
            width: 190px;
        }
    }
}
@media screen and (max-width: 768px) {
    #header {
        padding-bottom: 50px;
        min-height: 400px; 
    }
    .header {
        padding-top: 0;
        position: relative;
        height: 50px;
        &__wrapper {
            max-width: 500px;
            position: relative;
            justify-content: start;
        }
        &__navigation {
            position: fixed;
            height: 50px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: rgba(50, 56, 72, .7);
            z-index: 5;
        }
        &__menu {
            position: fixed;
            top: 0;
            height: 100vh;
            left: -100%;
            width: 50%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 20px 15px;
            margin-left: 0;
            background-color: rgba(0,0,0,.8);
            transition: 1s all;
            text-align: center;
            font-size: 18px;
            &_active {
                left: 0;
            }
            a {
                padding: 8px 0;
                border-right: none;
                display: block;
                width: 100%;
            }
        }
        &__logo {
            margin-bottom: 20px;
        }
        .hamburger {
            display: block;
            position: absolute;
            width: 20px;
            height: 12px;
            left: 50%;
            transform: translateX(-50%);
            div {
                background-color: #fff;
                width: 20px;
                height: 2px;
                margin: 3px;
                transition: .5s all;
            }
            &_active {
                div {
                    margin-bottom: -5px;
                    &:nth-child(1) {
                        transform: translateY(3px) rotate(-45deg);
                    }
                    &:nth-child(2) {
                        display: none;
                    }
                    &:nth-child(3) {
                        transform: translateY(3px) rotate(45deg);
                    }
                }
            }
        }
        .call__button {
            display: none;
        }
    }
    .main {
        &__icons {
            display: none;
        }
    }
}
@media screen and (max-width: 576px) {
    .main {
        &__header {
            font-size: 30px;
            line-height: 30px;
        }
        &__subheader {
            font-size: 24px;
            line-height: 24px;
        }
    }
}