.dilevery {
    min-height: 955px;
    padding: 53px 0 58px 0;
    &__cards {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 30px;
    }
    &__item {
        height: 365px;
        width: 265px;
        text-align: center;
        border: 1px solid #eaeaea;
        &__title {
            margin: 1px;
            font-weight: 700;
            text-transform: uppercase;
            background-color: $background_dark_color;
            width: 260px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
        }
        &__icon {
            margin-top: 16px;
        }
        &__price {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        &__descr {
            color: $form_text-color;
        }
        &__days {
            font-size: 14px;
            font-weight: 400;
            margin-top: 15px;
            text-transform: uppercase;
        }
        &__cost {
            color: $main_color;
            font-size: 24px;
            letter-spacing: 1px;
            line-height: 24px;
        }
        &__text {
            font-size: 14px;
            font-weight: 300;
            margin-top: 17px;
        }
        .btn_card {
            font-size: 12px;
            margin: 20px 0;
        }
    }
    &__form {
        &__title {
            color: $form_text-color;
            font-weight: 400;
            font-size: 28px;
            text-align: center;
            margin-top: 55px;
        }
        &__wrapper {
            position: relative;
            margin-top: 70px;
        }
        &__round {
            margin-bottom: 0;
        }
        &__label {
            position: absolute;
            top: -50px;
            left: 50%;
            transform: translateX(-50%);
            padding: 0 10px;
            background-color: #fff;
            &_btn {
                top: 85%;
                padding: 0px 13px;
            }
        }
        form {
            height: 165px;
            border: 1px solid #c4c4c4;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}
@media screen and (max-width: 1200px) {
    .dilevery {
        &__cards {
            flex-wrap: wrap;
            justify-content: center;
        }
        &__item {
            margin: 5px;
        }
    }
}
@media screen and (max-width: 768px) {
    .dilevery {
        &__form {
            form {
                height: 280px;
                width: 300px;
                flex-direction: column;
                margin: 0 auto;
                input {
                    margin: 7px 0;
                }
            }
            &__label {
                &_btn {
                    top: 90%;
                }
            }
        }
    }
}