.btn {
    width: 160px;
    height: 45px;
    color: #fff;
    background-color: $main_color; 
    border: none;
    border-radius: 16px;
    text-transform: uppercase;
    transition: .3s all;
    &:hover {
        box-shadow: 3px 3px 20px $main_color;
    }
    &_big {
        width: 230px;
        height: 57px;
        border-radius: 20px;
    }
    &_form {
        width: 180px;
        font-size: 12px;
    }
}
.call__button {
    font-size: 12px;
    margin-left: 19px;
}
.contacts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: end;
    margin-left: 46px;
    &__question {
        font-weight: 300;
        font-size: 14px;
        color: #fff;
        padding-left: 68px;
    }
    &__phone {
        font-weight: 700;
        font-size: 19px;
        color: #fff;
        &:hover {
            color: #fff;
            text-decoration: none;
        }
    }
}
.title {
    font-size: 26px;
    line-height: 26px;
    font-weight: 700;
    color: #2e2e2e;
    text-transform: uppercase;
    text-align: center;
    &__orange {
        color: $main_color;
        font-weight: 700;
        font-size: 14px;
    }
}

.divider {
    margin: 0 auto;
    width: 148px;
    height: 1px;
    background-color: $main_color;
    margin-bottom: 15px;
    margin-top: 25px;
}
.subtitle {
    text-align: center;
    font-size: 21px;
    font-weight: 300;
}
.round {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 20px;
    &_orange {
        border: 1px solid $main_color;
    }
}
form {
    input {
        width: 270px;
        height: 45px;
        border: 1px solid #c9c9c9;
        border-radius: 18px;
        padding-left: 28px;
        margin-right: 4px;
        margin-top: 16px;
        font-weight: 300;
        &:last-child {
            margin-right: 0;
        }
        &:focus {
            outline: none;
            border: 2px solid $main_color;
        }
    }
}
.agent, .manager {
    min-height: 345px;
    padding: 58px 0 75px 0;
}
.small-section__descr {
    color: #fff;
    text-align: center;
    max-width: 990px;
    margin: 0 auto;
}
.small-section__title {
    font-size: 26px;
    font-weight: 700;
    margin-bottom: 22px;
}
.small-section__text {
    font-weight: 300;
    font-size: 19px;
}
@media screen and(max-width: 1200px) {
    .container {
        max-width: 900px;
    }
    .contacts {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 10px;
        &__question {
            font-weight: 300;
            font-size: 14px;
            color: #fff;
            padding-left: 0px;
        }
        &__phone {
            font-weight: 700;
            font-size: 14px;
            color: #fff;
            &:hover {
                color: #fff;
                text-decoration: none;
            }
        }
    }
    .btn {
        width: 130px;
        font-size: 12px;
    }
}
@media screen and (max-width: 992px) {
    .container {
        max-width: 750px
    }
    .subtitle {
        font-size: 17px;
    }
}
@media screen and (max-width: 768px) {
    .container {
        max-width: 500px
    }
    .small-section__title {
        font-size: 24px;
    }
    .small-section__text {
        font-size: 16px;
    }
}
@media screen and (max-width: 576px) {
    .title {
        font-size: 22px;
    }
}