.sheme {
    min-height: 450px;
    padding: 51px 0 45px 0;
    background-color: $background_color;
    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    &__item {
        width: 250px;
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        text-align: center;
        margin-right: 35px;
        margin-top: 30px;
    }
    &__descr {
        margin-top: 20px;
        font-size: 14px;
        font-weight: 300;
    }
    &__text {
        margin-top: 8px;
    }
}
@media screen and (max-width: 768px) {
    .sheme {
        &__item {
            margin-right: 0;
        }
    }
}