.footer {
    background-color: $background_dark_color;
    min-height: 200px;
    padding: 24px 0 36px 0;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;
    }
    &__descr {
        color: #fff;
        font-weight: 700;
        margin-left: 45px;
    }
    &__links {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #a8adb1;
        margin-top: 19px;
    }
    &__link {
        color: $main_color;
        text-decoration: underline;
        transition: .2s all;
        &:hover {
            color: $main_color;
            opacity: 0.85;
        }
    }
    hr {
        border-color: #677177;
    }
}
@media screen and (max-width: 992px) {
    .footer {
        &__descr {
            font-size: 14px;
            text-align: center;
            margin-left: 25px;
        }
        .call__button {
            margin-left: 10px;
        }
        &__img {
            display: none;
        }
        &__wrapper {
            flex-direction: column;
        }
        .contacts {
            flex-direction: row;
            margin: 10px;
            &__question {
                margin-right: 15px;
            }
        }
    }
}
@media screen and (max-width: 576px) {
    .footer {
        &__links {
            flex-direction: column;
        }
        &__link {
            margin: 7px;
        }
    }
}