.carousel {
    min-height: 470px;
    padding: 50px 0 65px 0;
    text-align: center;
}
.carousel__inner {
    display: flex;
    position: relative;
    margin-top: 25px;
}
.slick-slide {
    text-align: center;
    transition: .5s all;
    opacity: 0.5;
    width: 600px;
    transform: scale(0.5);
    img {
        display: block;
        margin: 0 auto;
    }
}
.slick-slide.slick-center {
    transform: scale(1);
}
.slick-slide.slick-active{
    height: 240px;
    opacity: 1;
} 
.slick-next, .slick-prev {
    position: absolute;
    border: none;
    background: none;
    z-index: 10;
    img {
        width: 14px;
        height: 45px;
    }
}
.slick-next {
    right: 40%;
    top: 30px;
}
.slick-prev {
    left: 40%;
    top: 30px;
}
@media screen and (max-width: 992px) {
    .slick-next {
        right: 35%;
    }
    .slick-prev {
        left: 35%;
    }
    .slick-slide {
        width: 400px;    
    }
}
@media screen and (max-width: 576px) {
    .slick-slide {
        width: 300px;    
    }
}