@media screen and (max-width: 768px) {
  .container {
    max-width: 500px;
  }

  .small-section__title {
    font-size: 24px;
  }

  .small-section__text {
    font-size: 16px;
  }

  #header {
    padding-bottom: 50px;
    min-height: 400px;
  }

  .header {
    padding-top: 0;
    position: relative;
    height: 50px;
  }

  .header__wrapper {
    max-width: 500px;
    position: relative;
    justify-content: start;
  }

  .header__navigation {
    position: fixed;
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(50, 56, 72, 0.7);
    z-index: 5;
  }

  .header__menu {
    position: fixed;
    top: 0;
    height: 100vh;
    left: -100%;
    width: 50%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 15px;
    margin-left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    transition: 1s all;
    text-align: center;
    font-size: 18px;
  }

  .header__menu_active {
    left: 0;
  }

  .header__menu a {
    padding: 8px 0;
    border-right: none;
    display: block;
    width: 100%;
  }

  .header__logo {
    margin-bottom: 20px;
  }

  .header .hamburger {
    display: block;
    position: absolute;
    width: 20px;
    height: 12px;
    left: 50%;
    transform: translateX(-50%);
  }

  .header .hamburger div {
    background-color: #fff;
    width: 20px;
    height: 2px;
    margin: 3px;
    transition: .5s all;
  }

  .header .hamburger_active div {
    margin-bottom: -5px;
  }

  .header .hamburger_active div:nth-child(1) {
    transform: translateY(3px) rotate(-45deg);
  }

  .header .hamburger_active div:nth-child(2) {
    display: none;
  }

  .header .hamburger_active div:nth-child(3) {
    transform: translateY(3px) rotate(45deg);
  }

  .header .call__button {
    display: none;
  }

  .main__icons {
    display: none;
  }

  .dilevery__form form {
    height: 280px;
    width: 300px;
    flex-direction: column;
    margin: 0 auto;
  }

  .dilevery__form form input {
    margin: 7px 0;
  }

  .dilevery__form__label_btn {
    top: 90%;
  }

  .sheme__item {
    margin-right: 0;
  }
}

